import { css } from '@emotion/react';
import type { ReactNode } from 'react';
import React from 'react';

import type { ButtonProps } from '@databricks/design-system';
import { Button, Tooltip } from '@databricks/design-system';

import { useSnippetActionsTheme } from './SnippetActionsTheme';

type SnippetActionButtonProps = Pick<ButtonProps, 'icon' | 'onClick'> & {
  tooltipMessage: NonNullable<ReactNode>;
};

// `!important` is required to override dubois styles
function importantNullableStyle(styleValue: string | undefined) {
  return styleValue && `${styleValue} !important`;
}

export default function SnippetActionButton({ tooltipMessage, ...buttonProps }: SnippetActionButtonProps) {
  const { color, hoverColor, hoverBackgroundColor, backgroundColor } = useSnippetActionsTheme();
  const style = css({
    zIndex: 1, // required for action buttons to be visible and float
    // theme
    color: importantNullableStyle(color),
    backgroundColor: importantNullableStyle(backgroundColor),
    ':hover': {
      color: importantNullableStyle(hoverColor),
      backgroundColor: importantNullableStyle(hoverBackgroundColor),
    },
  });
  return (
    <Tooltip title={tooltipMessage}>
      <Button {...buttonProps} css={style} />
    </Tooltip>
  );
}
