import React, { useEffect, useRef, useState } from 'react';
import { useClipboard } from 'use-clipboard-copy';

import { CopyIcon, CheckIcon } from '@databricks/design-system';
import { FormattedMessage } from '@databricks/i18n';

import SnippetActionButton from './SnippetActionButton';

export interface SnippetCopyActionProps {
  /**
   * The text to be copied into clipboard when action button is clicked.
   */
  copyText: string;
}

const copyTooltipMessage = (
  <FormattedMessage
    defaultMessage="Copy"
    description="Snippet Copy Action: tooltip displayed on the action button hover"
  />
);
const copiedTooltipMessage = (
  <FormattedMessage
    defaultMessage="Copied"
    description="Snippet Copy Action: tooltip displayed the action button hover, after user clicks on it"
  />
);

export function SnippetCopyAction({ copyText }: SnippetCopyActionProps) {
  const clipboard = useClipboard();
  const copiedTimerIdRef = useRef<number>();
  const [copied, setCopied] = useState(false);

  useEffect(() => {
    return () => {
      window.clearTimeout(copiedTimerIdRef.current);
    };
  }, []);

  return (
    <SnippetActionButton
      tooltipMessage={copied ? copiedTooltipMessage : copyTooltipMessage}
      icon={copied ? <CheckIcon /> : <CopyIcon />}
      onClick={() => {
        clipboard.copy(copyText);
        window.clearTimeout(copiedTimerIdRef.current);
        setCopied(true);
        copiedTimerIdRef.current = window.setTimeout(() => {
          setCopied(false);
        }, 3000);
      }}
    />
  );
}
