import type { ReactNode } from 'react';
import React, { useContext } from 'react';

export interface SnippetActionsTheme {
  color?: string;
  hoverColor?: string;
  backgroundColor?: string;
  hoverBackgroundColor?: string;
}

export interface SnippetActionsThemeProviderProps {
  theme?: SnippetActionsTheme;
  children: ReactNode;
}

const SnippetActionsThemeContext = React.createContext<SnippetActionsTheme>({});

export function SnippetActionsThemeProvider({ children, theme = {} }: SnippetActionsThemeProviderProps) {
  return <SnippetActionsThemeContext.Provider value={theme}>{children}</SnippetActionsThemeContext.Provider>;
}

export function useSnippetActionsTheme() {
  return useContext(SnippetActionsThemeContext);
}
