import type { ReactNode } from 'react';
import React from 'react';

import { FullscreenExitIcon, FullscreenIcon } from '@databricks/design-system';
import { FormattedMessage } from '@databricks/i18n';

import SnippetActionButton from './SnippetActionButton';

export interface SnippetFullScreenActionProps {
  /**
   * Whether snippet is displayed in full screen mode or not
   *  If this is true, full screen exit icon is displayed instead
   */
  fullScreen: boolean;
  onClick: (isInFullScreen: boolean) => void;
  enterFullScreenTooltip?: NonNullable<ReactNode>;
  exitFullScreenTooltip?: NonNullable<ReactNode>;
}

const defaultEnterFullScreenTooltip = (
  <FormattedMessage
    defaultMessage="Enter fullscreen"
    description="Snippet fullscreen/expand Action: tooltip displayed on the action button hover"
  />
);
const defaultExitFullScreenTooltip = (
  <FormattedMessage
    defaultMessage="Exit fullscreen"
    description="Snippet fullscreen/expand Action: tooltip displayed on action hover, when already in fullscreen mode"
  />
);

export function SnippetFullScreenAction({
  fullScreen,
  onClick,
  exitFullScreenTooltip = defaultExitFullScreenTooltip,
  enterFullScreenTooltip = defaultEnterFullScreenTooltip,
}: SnippetFullScreenActionProps) {
  return (
    <SnippetActionButton
      tooltipMessage={fullScreen ? exitFullScreenTooltip : enterFullScreenTooltip}
      icon={fullScreen ? <FullscreenExitIcon /> : <FullscreenIcon />}
      onClick={() => {
        onClick(!fullScreen);
      }}
    />
  );
}
