import { css } from '@emotion/react';
import type { ReactNode } from 'react';
import React from 'react';

import { useDesignSystemTheme } from '@databricks/design-system';

import SnippetActionsContainer from './actions/SnippetActionsContainer';
import { SnippetActionsThemeProvider } from './actions/SnippetActionsTheme';
import { duboisAlertBackgroundColor, snippetPadding } from './styles';

const preformattedMessageStyle = css`
  font-family: monospace;
  margin: 0;
  border: none;
  height: 100%;
  color: inherit;
  background-color: inherit;
  padding: ${snippetPadding}px;
`;

const containerStyle = css`
  overflow: auto;
  height: 100%;
`;

export interface ErrorSnippetProps {
  className?: string;
  /**
   * The error message
   */
  children: string;
  /**
   * The actions that are displayed on the right top corner of the component
   *  see `./actions` for built-in supported actions
   */
  actions?: ReactNode | ReactNode[];
}

/**
 * `ErrorSnippet` is used for displaying large error message, stack-trace  as a data.
 *
 * WARN: It is not meant to be used as an error alert, when you need to display message of a error that
 *  happened after user interaction such as form submit, run a query from the UI, network request errors.
 *  For these cases where you need to provide contextual feedback to a user action, use `Alert` from `dubois`.
 */
export function ErrorSnippet({ children, className, actions }: ErrorSnippetProps) {
  const { theme } = useDesignSystemTheme();
  return (
    <>
      <SnippetActionsContainer>
        <SnippetActionsThemeProvider
          theme={{
            color: theme.colors.red600,
            hoverColor: theme.colors.red600,
            hoverBackgroundColor: theme.colors.red200,
          }}
        >
          {actions}
        </SnippetActionsThemeProvider>
      </SnippetActionsContainer>
      <div
        role="alert"
        css={[
          containerStyle,
          { color: theme.colors.actionDangerBackgroundDefault, backgroundColor: duboisAlertBackgroundColor },
        ]}
      >
        <pre css={preformattedMessageStyle} className={className}>
          {children}
        </pre>
      </div>
    </>
  );
}
