import { css } from '@emotion/react';
import type { ReactNode } from 'react';
import React from 'react';

// HACK: `margin-right` used to handle when snippet has a scroll bar
const actionContainerStyle = css`
  margin-right: 16px;
  text-align: right;
  position: absolute;
  right: 0;
  padding-top: 2px;
`;

interface SnippetActionsContainerProps {
  children: ReactNode;
}

export default function SnippetActionsContainer({ children }: SnippetActionsContainerProps) {
  return <div css={actionContainerStyle}>{children}</div>;
}
