import { pick } from 'lodash';
import type { CSSProperties, ReactNode } from 'react';
import React from 'react';
import { PrismLight as SyntaxHighlighter } from 'react-syntax-highlighter';

import SnippetActionsContainer from './actions/SnippetActionsContainer';
import { SnippetActionsThemeProvider } from './actions/SnippetActionsTheme';
import sql from './lang/sql';
// TODO: add custom Databricks themes (that matches with DBSQL Query Editor and/or Notebooks)
import {
  buttonBackgroundColorDark,
  buttonColorDark,
  buttonHoverBackgroundColorDark,
  buttonHoverColorDark,
  snippetPadding,
} from './styles';
import duotoneDarkStyle from './theme/databricks-duotone-dark';
import lightStyle from './theme/databricks-light';

SyntaxHighlighter.registerLanguage('sql', sql);

export type CodeSnippetLanguage = 'sql';
export type CodeSnippetTheme = 'duotoneDark' | 'light';

const themesStyles: Record<CodeSnippetTheme, any> = {
  light: lightStyle,
  duotoneDark: duotoneDarkStyle,
};

export interface CodeSnippetProps {
  /**
   * The code string
   */
  children: string;
  /**
   * The actions that are displayed on the right top corner of the component
   *  see `./actions` for built-in actions
   */
  actions?: NonNullable<ReactNode> | NonNullable<ReactNode>[];
  /**
   * The theme, default theme is `light`
   */
  theme?: CodeSnippetTheme;
  /**
   * Language of the code (`children`)
   */
  language: CodeSnippetLanguage;
  /**
   * Custom styles (passed to the internal `<pre>`)
   */
  style?: CSSProperties;
  /**
   * Whether to show line numbers on the left or not
   */
  showLineNumbers?: boolean;
  /**
   * Custom styles for line numbers
   */
  lineNumberStyle?: CSSProperties;
}

/**
 * `CodeSnippet` is used for highlighting code, use this instead of
 */
export function CodeSnippet({
  theme = 'light',
  language,
  actions,
  style,
  children,
  showLineNumbers,
  lineNumberStyle,
}: CodeSnippetProps) {
  const customStyle = {
    border: 'none',
    borderRadius: 0,
    margin: 0,
    padding: snippetPadding,
    ...style,
  };

  // TODO(dubois): use dubois dark theme directly instead of this hack, when they are available for all FEs
  // this is done in order not to bring all dark theme styles for only action styles
  const actionsTheme =
    theme === 'duotoneDark'
      ? {
          color: buttonColorDark,
          hoverColor: buttonHoverColorDark,
          backgroundColor: buttonBackgroundColorDark,
          hoverBackgroundColor: buttonHoverBackgroundColorDark,
        }
      : undefined;

  return (
    <>
      {/* TODO: consider introducing larger padding-top when `actions` prop is present  */}
      <SnippetActionsContainer>
        <SnippetActionsThemeProvider theme={actionsTheme}>{actions}</SnippetActionsThemeProvider>
      </SnippetActionsContainer>
      <SyntaxHighlighter
        showLineNumbers={showLineNumbers}
        lineNumberStyle={lineNumberStyle}
        language={language}
        style={themesStyles[theme]}
        customStyle={customStyle}
        codeTagProps={{
          style: pick(style, 'backgroundColor'),
        }}
      >
        {children}
      </SyntaxHighlighter>
    </>
  );
}
